





























import * as THREE from 'three'
import Vue from 'vue'
import { FrontPageManager } from '../scripts/FrontPage4.0/Scene'
// import ProxyConfig from '../scripts/FrontPage4.0/ProxyConfig'
import { testStyle1, testStyle2, testStyle3, testStyle4, testStyle5 } from '../scripts/FrontPage4.0/ProxyStyles/TestStyles'

type Data = {
    fpm: FrontPageManager | null,
    loadingStat: {
        loaded: number,
        total: number,
        isLoaded: boolean,
        noLoadingStat: boolean
    }
}

export default Vue.extend({
    data(): Data {
        return {
            fpm: null,
            loadingStat: {
                loaded: 0,
                total: 1,
                isLoaded: false,
                noLoadingStat: false
            }
        }
    },
    mounted() {
        const self = this
        // for (let i = 0; i < 10; i++) {
        //     this.cardInfos.push(
        //         {
        //             imgUrl: `card${Math.floor(i%3)+1}.jpg`,
        //             url: 'fakeUrl_' + i
        //         }
        //     )
        // }
        if (this.$refs['canvas-container'] instanceof HTMLElement) {
            this.fpm = new FrontPageManager(
                this.$refs['canvas-container'],
                // this.cardInfos,
                () => {
                    console.log('loaded')
                    self.loadingStat.isLoaded = true
                    setTimeout(() => {
                        self.loadingStat.noLoadingStat = true
                    }, 501)
                },
                (_, loaded, total) => {
                    this.loadingStat.loaded = loaded
                    this.loadingStat.total = total
                },
                (url) => {
                    console.error('failed to load asset: ' + url)
                    self.loadingStat.isLoaded = true
                },
                (index) => {
                    console.log('focused on card: ', index)
                }
            )
            // if (this.$refs['dom3d-container'] instanceof HTMLElement && this.$refs['dom3d-test'] instanceof HTMLElement) {
            //     this.fpm.registerCSSCoordinator(this.$refs['dom3d-container'], this.$refs['dom3d-test'], new THREE.Vector3(-200, 200, 0))
            // }

            this.fpm.registerNewProxyGroup(testStyle5())
            // this.fpm.registerNewProxyGroup(testStyle2())
            this.fpm.registerNewProxyGroup(testStyle3())
            this.fpm.registerNewProxyGroup(testStyle4())
            this.fpm.initCards()

        }
    },
    methods: {
        onGlassClick() {
            alert('glass clicked!')
        },

        swapLeft() {
            this.fpm?.swapProxyGroup(true)
        },

        swapRight() {
            this.fpm?.swapProxyGroup(false)
        }
    }
})
