import * as THREE from 'three'
import { ContainerProxyConfig } from './ProxyConfig'

export default class CardContainer {

    // position: v - velocity, s - state, t - target
    p_v: THREE.Vector3
    p_s: THREE.Vector3
    p_t: THREE.Vector3

    // rotation (represented with quaternions)
    r_v: THREE.Vector3
    r_s: THREE.Vector3
    r_t: THREE.Vector3

    r_offset: number

    targetAttraction: number
    momentum: number
    damp: number

    // if true, the card is very close to its target proxy
    isStatic: boolean
    staticThreshold: number

    cardGroup: THREE.Group


    constructor(targetAttraction: number = 0.04, momentum: number = 1.2, damp: number = 0.4) {
        this.p_v = new THREE.Vector3()
        this.p_s = new THREE.Vector3()
        this.p_t = new THREE.Vector3()

        this.r_v = new THREE.Vector3()
        this.r_s = new THREE.Vector3()
        this.r_t = new THREE.Vector3()

        this.targetAttraction = targetAttraction
        this.momentum = momentum
        this.damp = damp

        this.isStatic = true
        this.staticThreshold = 1e-3

        this.r_offset = 0

        this.cardGroup = new THREE.Group()
        var markerCube = new THREE.Mesh(new THREE.BoxGeometry(10,10,10), new THREE.MeshNormalMaterial())
        this.cardGroup.add(markerCube)
    }

    setTargetProxy(proxyConfig: ContainerProxyConfig): void {
        this.p_t = proxyConfig.position
        // let quat = new THREE.Quaternion().setFromEuler(proxyConfig.rotation)
        // this.r_t.set(quat.x, quat.y, quat.z, quat.w)
        this.r_t.set(proxyConfig.rotation.x, proxyConfig.rotation.y + this.r_offset*Math.PI * 2, proxyConfig.rotation.z)
        this.isStatic = false
    }

    initSet(proxyConfig: ContainerProxyConfig): void {
        this.setTargetProxy(proxyConfig)
        
        this.p_s.copy(this.p_t)
        this.r_s.copy(this.r_t)

        this.cardGroup.position.copy(this.p_s)
        this.cardGroup.rotation.set(this.r_s.x, this.r_s.y, this.r_s.z)
        // this.cardGroup.rotation.setFromQuaternion(new THREE.Quaternion(this.r_s.x, this.r_s.y, this.r_s.z, this.r_s.w))
    }


    update(): void {
        if (!this.isStatic) {
            // position
            this.p_v.multiplyScalar(this.damp)
            this.p_v.add((new THREE.Vector3()).copy(this.p_t).sub(this.p_s).multiplyScalar(this.targetAttraction))
            this.p_s.add(this.p_v)
            
            // rotation
            this.r_v.multiplyScalar(this.damp)
            this.r_v.add((new THREE.Vector3()).copy(this.r_t).sub(this.r_s).multiplyScalar(this.targetAttraction))
            this.r_s.add(this.r_v)

            // compute static
            if (this.p_v.length() < this.staticThreshold
                && this.r_v.length() < this.staticThreshold) {
                this.isStatic = true
            }

            // assign values to mesh
            this.cardGroup.position.copy(this.p_s)
            this.cardGroup.rotation.set(this.r_s.x, this.r_s.y, this.r_s.z)
        }
    }
}