import * as THREE from 'three'
import { ContainerProxyConfig, CardProxyConfig } from './ProxyConfig'
import Card from './Card'
import CardContainer from './CardContainer'
import TexturePool from './TexturePool'

class ProxyGroup {

    isCurrentFocused: boolean // 当前是否处于有卡片被选中的状态
    currentFocusIndex: number // 当前被选中的卡片 index （只有 isCurrentFocused == true 时有意义）
    visibleCardCount: number  // 场景中可见的最大卡片数量
    isLoop: boolean   // 如果为 true，滚动切换 index 时 index 循环更新（如 Carousel 中的无限滚动）；否则滚动到第一个或最后一个卡片时，不继续向前或向后

    cardContainer: CardContainer | undefined
    containerProxyConfig: ContainerProxyConfig

    cardProxyConfigs: Array<CardProxyConfig>
    onStatic: (containerProxyConfig: ContainerProxyConfig, cardProxyConfigs: Array<CardProxyConfig>) => any  // ProxyGroup 的静态状态设定（未选中任何卡片时的状态）
    onFocusIndex: (containerProxyConfig: ContainerProxyConfig, cardProxyConfigs: Array<CardProxyConfig>,
        isCurrentFocused: boolean, oldFocusIndex: number, newFocusIndex: number) => any           // 选中某一卡片时的状态设定（未选中任何卡片时的状态）
    
    cards: Array<Card>
    invisibleCardProxy: CardProxyConfig

    constructor (visibleCardCount: number, isLoop: boolean = false,
        onStatic: (containerProxyConfig: ContainerProxyConfig, cardProxyConfigs: Array<CardProxyConfig>) => any = () => undefined,
        onFocusIndex: (containerProxyConfig: ContainerProxyConfig, cardProxyConfigs: Array<CardProxyConfig>,
            isCurrentFocused: boolean, oldFocusIndex: number, newFocusIndex: number) => any = () => undefined,
        ) {
        this.isCurrentFocused = false
        this.currentFocusIndex = 0
        this.visibleCardCount = visibleCardCount

        this.cardContainer = undefined
        this.containerProxyConfig = new ContainerProxyConfig()

        this.cardProxyConfigs = []
        for (let i = 0; i < visibleCardCount; i++) {
            this.cardProxyConfigs.push(new CardProxyConfig())
        }

        this.isLoop = isLoop
        this.onStatic = onStatic
        this.onFocusIndex = onFocusIndex
        this.cards = []
        this.invisibleCardProxy = new CardProxyConfig()
    }

    /**
     * 场景切换到 static 状态
     */
    toStatic() {
        this.isCurrentFocused = false
        this.currentFocusIndex = 0
        this.onStatic(this.containerProxyConfig, this.cardProxyConfigs)
        this.refreshTargets()
    }

    /**
     * focus 到下一张卡片；如果没有 focus 的卡片 (状态为 static)，则 focus 第一张卡片
     */
     focusForward() {
        let newFocusIndex = 0
        if (this.isCurrentFocused) {
            newFocusIndex = this.currentFocusIndex + 1
            if (newFocusIndex >= this.visibleCardCount) {
                if (this.isLoop) {
                    newFocusIndex = 0
                } else {
                    return
                }
            }
        }
        this.focusOnCard(newFocusIndex)
    }

    /**
     * focus 到上一张卡片；如果没有 focus 的卡片 (状态为 static)，则 focus 最后一张卡片
     */
     focusBackward() {
        let newFocusIndex = this.visibleCardCount - 1
        if (this.isCurrentFocused) {
            newFocusIndex = this.currentFocusIndex - 1
            if (newFocusIndex < 0) {
                if (this.isLoop) {
                    newFocusIndex = this.visibleCardCount - 1
                } else {
                    return
                }
            }
        }
        this.focusOnCard(newFocusIndex)
    }

    /**
     * focus 到指定的卡片
     */
    focusOnCard(focusIndex: number) {
        if (focusIndex < 0 || focusIndex >= this.visibleCardCount) {
            return
        }
        this.onFocusIndex(this.containerProxyConfig, this.cardProxyConfigs, this.isCurrentFocused, this.currentFocusIndex, focusIndex)
        this.isCurrentFocused = true
        this.currentFocusIndex = focusIndex
        this.refreshTargets()
    }

    refreshTargets() {
        let cardIndex = 0
        this.cardContainer?.setTargetProxy(this.containerProxyConfig)
        for (; cardIndex < this.visibleCardCount; cardIndex++) {
            this.cards[cardIndex].setTargetProxy(this.cardProxyConfigs[cardIndex])
        }
        for (; cardIndex < this.cards.length; cardIndex++) {
            this.cards[cardIndex].setTargetProxy(this.invisibleCardProxy)
        }
    }

}

class ProxyManager {

    proxyGroups: Array<ProxyGroup>
    maxCardCount: number
    currentProxyGroupIndex: number

    containerProxyConfig: ContainerProxyConfig
    cardContainer: CardContainer
    
    cards: Array<Card>
    texturePool: TexturePool | undefined

    constructor () {
        this.proxyGroups = []
        this.maxCardCount = 0
        this.currentProxyGroupIndex = 0

        this.containerProxyConfig = new ContainerProxyConfig()
        this.cardContainer = new CardContainer()

        this.cards = []
        this.texturePool = undefined
    }

    registerProxyGroup (proxyGroup: ProxyGroup) {
        this.proxyGroups.push(proxyGroup)
        proxyGroup.cardContainer = this.cardContainer
        proxyGroup.containerProxyConfig = this.containerProxyConfig
        this.cardContainer.r_s.x %= Math.PI * 2
        this.cardContainer.r_s.y %= Math.PI * 2
        this.cardContainer.r_s.z %= Math.PI * 2
        this.cardContainer.cardGroup.rotation.set(this.cardContainer.r_s.x, this.cardContainer.r_s.y, this.cardContainer.r_s.z)
        this.cards.forEach(card => {
            card.r_s.x %= Math.PI * 2
            card.r_s.y %= Math.PI * 2
            card.r_s.z %= Math.PI * 2
        });
        if (proxyGroup.visibleCardCount > this.maxCardCount) {
            this.maxCardCount = proxyGroup.visibleCardCount
        }
    }

    swapProxyGroup (newProxyGroupIndex: number) {
        if (newProxyGroupIndex < 0 || newProxyGroupIndex >= this.proxyGroups.length) {
            // out of bounds
            console.log('out of bounds!!')
            return
        }
        this.cardContainer.r_offset = newProxyGroupIndex
        this.currentProxyGroupIndex = newProxyGroupIndex
        this.proxyGroups[this.currentProxyGroupIndex].toStatic()
    }

    /**
     * 完成所有 registerProxyGroup 后，生成 cards
     * @param cardParent 
     */
    initCards (cardParent: THREE.Object3D) {
        // 卡片正反面各一个材质，因此存在场景中的最大材质数为 2 * maxCardCount
        this.texturePool = new TexturePool(this.cards, this.maxCardCount * 2)
        for (let i = 0; i < this.maxCardCount; i++) {
            const card = new Card(this.cardContainer.cardGroup, this.texturePool)
            // card.initSet(this.invisibleCardProxy)
            this.cards.push(card)
        }
        this.proxyGroups.forEach(proxyGroup => {
            proxyGroup.cards = this.cards
        })
        cardParent.add(this.cardContainer.cardGroup)
        this.proxyGroups[this.currentProxyGroupIndex].refreshTargets()
        this.toStatic()
    }

    update() {
        this.cardContainer.update()
        this.cards.forEach(card => {
            card.update()
        })
    }

    toStatic() {
        if (this.proxyGroups[this.currentProxyGroupIndex].isCurrentFocused) {
            this.proxyGroups[this.currentProxyGroupIndex].toStatic()
        }
    }
    focusForward() {
        this.proxyGroups[this.currentProxyGroupIndex].focusForward()
    }
    focusBackward() {
        this.proxyGroups[this.currentProxyGroupIndex].focusBackward()
    }
    focusOnCard(focusMesh: THREE.Object3D) {
        let cardIndex = 0
        for (; cardIndex < this.cards.length; cardIndex++) {
            if (this.cards[cardIndex].cardMeshFront == focusMesh || this.cards[cardIndex].cardMeshBack == focusMesh) {
                this.proxyGroups[this.currentProxyGroupIndex].focusOnCard(cardIndex)
                break
            }
        }
    }
}

export { ProxyGroup, ProxyManager }