import * as THREE from 'three'

export class ContainerProxyConfig {
    position: THREE.Vector3
    rotation: THREE.Euler

    constructor () {
        this.position = new THREE.Vector3(0, 0, 0)
        this.rotation = new THREE.Euler(0, 0, 0)
    }
}

export class CardProxyConfig extends ContainerProxyConfig {
    scale: THREE.Vector2
    frontColor: THREE.Color
    frontImgUrl: string
    backColor: THREE.Color
    backImgUrl: string

    constructor () {
        super()
        this.scale = new THREE.Vector2(0, 0)
        this.frontColor = new THREE.Color(0, 0, 0)
        this.frontImgUrl = ''
        this.backColor = new THREE.Color(0, 0, 0)
        this.backImgUrl = ''
    }
}