import * as THREE from 'three'
import { CardProxyConfig } from './ProxyConfig'
import TexturePool from './TexturePool'


export default class Card {

    parent: THREE.Object3D

    // position: v - velocity, s - state, t - target
    p_v: THREE.Vector3
    p_s: THREE.Vector3
    p_t: THREE.Vector3

    // rotation (represented with quaternions)
    r_v: THREE.Vector4
    r_s: THREE.Vector4
    r_t: THREE.Vector4

    // scale
    s_v: THREE.Vector2
    s_s: THREE.Vector2
    s_t: THREE.Vector2

    targetAttraction: number
    momentum: number
    damp: number

    // if true, the card is very close to its target proxy
    isStatic: boolean
    staticThreshold: number

    // card object
    cardGeometry: THREE.PlaneBufferGeometry
    cardFrontBackGroup: THREE.Group
    cardMeshFront: THREE.Mesh
    cardMeshBack: THREE.Mesh

    // materials
    texturePool: TexturePool
    matFront: THREE.MeshBasicMaterial
    matBack: THREE.MeshBasicMaterial
    matFrontImgUrl: string
    matBackImgUrl: string
    colorWithMap: THREE.Color

    constructor(parent: THREE.Object3D, texturePool: TexturePool, targetAttraction: number = 0.04, momentum: number = 1.2, damp: number = 0.4) {
        this.parent = parent
        this.p_v = new THREE.Vector3()
        this.p_s = new THREE.Vector3()
        this.p_t = new THREE.Vector3()

        this.r_v = new THREE.Vector4()
        this.r_s = new THREE.Vector4()
        this.r_t = new THREE.Vector4()

        this.s_v = new THREE.Vector2()
        this.s_s = new THREE.Vector2()
        this.s_t = new THREE.Vector2()

        this.targetAttraction = targetAttraction
        this.momentum = momentum
        this.damp = damp

        this.isStatic = true
        this.staticThreshold = 1e-3

        // material
        this.texturePool = texturePool
        this.matFront = new THREE.MeshBasicMaterial({
            color: new THREE.Color(1, 0, 0),
            map:  texturePool.getTexture(),
            side: THREE.FrontSide
        })
        this.matBack = new THREE.MeshBasicMaterial({
            color: new THREE.Color(0, 1, 0),
            map: texturePool.getTexture(),
            side: THREE.BackSide
        })
        this.matFrontImgUrl = ''
        this.matBackImgUrl = ''
        this.colorWithMap = new THREE.Color( 0xffffff )

        this.cardGeometry = new THREE.PlaneGeometry(1, 1)
        // this.cardMesh = new THREE.Mesh(this.cardGeometry, [
        //     this.matFront, this.matBack
        // ])

        this.cardMeshFront = new THREE.Mesh(this.cardGeometry, this.matFront)
        this.cardMeshBack = new THREE.Mesh(this.cardGeometry, this.matBack)

        this.cardFrontBackGroup = new THREE.Group()
        this.cardFrontBackGroup.add(this.cardMeshFront)
        this.cardFrontBackGroup.add(this.cardMeshBack)
        this.parent.add(this.cardFrontBackGroup)
    }

    setTargetProxy(proxyConfig: CardProxyConfig): void {
        this.p_t = proxyConfig.position
        // proxyConfig.rotation.set(
        //     proxyConfig.rotation.x == 0 ? 5e-2: proxyConfig.rotation.x,
        //     proxyConfig.rotation.y == 0 ? 5e-2: proxyConfig.rotation.y,
        //     proxyConfig.rotation.z == 0 ? 5e-2: proxyConfig.rotation.z,
        // )
        let quat = new THREE.Quaternion().setFromEuler(proxyConfig.rotation)
        // if (quat.x == 0) {
        //     quat.x = 1e-3
        // }
        // if (quat.y == 0) {
        //     quat.y = 1e-3
        // }
        // if (quat.z == 0) {
        //     quat.z = 1e-3
        // }
        // if (quat.w == 0) {
        //     quat.w = 1e-3
        // }
        this.r_t.set(quat.x, quat.y, quat.z, quat.w)
        this.s_t = proxyConfig.scale
        this.isStatic = false
        this.changeMaterial(proxyConfig)
    }

    initSet(proxyConfig: CardProxyConfig): void {
        this.setTargetProxy(proxyConfig)
        
        this.p_s.copy(this.p_t)
        this.r_s.copy(this.r_t)
        this.s_s.copy(this.s_t)

        this.cardFrontBackGroup.position.copy(this.p_s)
        this.cardFrontBackGroup.rotation.setFromQuaternion(new THREE.Quaternion(this.r_s.x, this.r_s.y, this.r_s.z, this.r_s.w))
        this.cardFrontBackGroup.scale.set(this.s_s.x, this.s_s.y, 1)
    }

    changeMaterial(proxyConfig: CardProxyConfig) {
        this.matFront.color = proxyConfig.frontImgUrl == '' ? proxyConfig.frontColor : this.colorWithMap
        this.matBack.color = proxyConfig.backImgUrl == '' ? proxyConfig.backColor : this.colorWithMap
        if (proxyConfig.frontImgUrl !== this.matFrontImgUrl) {
            if (proxyConfig.frontImgUrl !== '') {
                this.matFront.map = this.texturePool.getTexture(proxyConfig.frontImgUrl)
            } else {
                this.matFront.map = this.texturePool.getTexture()
            }
        }
        if (proxyConfig.backImgUrl !== this.matBackImgUrl) {
            if (proxyConfig.backImgUrl !== '') {
                this.matBack.map = this.texturePool.getTexture(proxyConfig.backImgUrl)
            } else {
                this.matBack.map = this.texturePool.getTexture()
            }
        }
        this.matFrontImgUrl = proxyConfig.frontImgUrl
        this.matBackImgUrl = proxyConfig.backImgUrl
    }

    update(): void {
        if (!this.isStatic) {
            // position
            this.p_v.multiplyScalar(this.damp)
            this.p_v.add((new THREE.Vector3()).copy(this.p_t).sub(this.p_s).multiplyScalar(this.targetAttraction))
            this.p_s.add(this.p_v)
            
            // rotation
            this.r_v.multiplyScalar(this.damp)
            this.r_v.add((new THREE.Vector4()).copy(this.r_t).sub(this.r_s).multiplyScalar(this.targetAttraction))
            this.r_s.add(this.r_v)

            // scale
            this.s_v.multiplyScalar(this.damp)
            this.s_v.add((new THREE.Vector2()).copy(this.s_t).sub(this.s_s).multiplyScalar(this.targetAttraction))
            this.s_s.add(this.s_v)

            // assign values to mesh
            this.cardFrontBackGroup.position.copy(this.p_s)
            this.cardFrontBackGroup.rotation.setFromQuaternion(new THREE.Quaternion(this.r_s.x, this.r_s.y, this.r_s.z, this.r_s.w))
            this.cardFrontBackGroup.scale.set(this.s_s.x, this.s_s.y, 1)

            // compute static
            if (this.p_v.length() < this.staticThreshold
                && this.r_v.length() < this.staticThreshold
                && this.s_v.length() < this.staticThreshold) {
                this.isStatic = true
            }
        }
    }
}