import * as THREE from 'three'
import { CardProxyConfig, ContainerProxyConfig } from '../ProxyConfig'
import { ProxyGroup } from '../Proxy'

export function testStyle1() {
    const visibleCardCount = 8
    const onStatic = function (containerProxyConfig: ContainerProxyConfig, cardProxyConfigs: Array<CardProxyConfig>) {
        containerProxyConfig.position.set(0, 0, 0)
        containerProxyConfig.rotation.set(0.1, 0.1, 0.5)
        for (let i = 0; i < visibleCardCount; i++) {
            cardProxyConfigs[i].position.set((i - (visibleCardCount-1) / 2) * 40, 0, 0)
            cardProxyConfigs[i].rotation.set(i, 0, i*0.2)
            cardProxyConfigs[i].scale.set(30, 30)
            cardProxyConfigs[i].frontColor.set(0xff0000)
            cardProxyConfigs[i].backColor.set(0x00ff00)
            cardProxyConfigs[i].frontImgUrl = ''
            cardProxyConfigs[i].backImgUrl = ''
        }
    }
    const onFocusIndex = function (containerProxyConfig: ContainerProxyConfig, cardProxyConfigs: Array<CardProxyConfig>,
        isCurrentFocused: boolean, oldFocusIndex: number, newFocusIndex: number) {
        
        for (let i = 0; i < visibleCardCount; i++) {
            if (i == newFocusIndex) {
                cardProxyConfigs[i].position.set((i - (visibleCardCount-1) / 2) * 40, 10, 0)
                cardProxyConfigs[i].rotation.set(0, 1, 0)
                cardProxyConfigs[i].scale.set(50, 50)
                cardProxyConfigs[i].frontColor.set(0xffff00)
                cardProxyConfigs[i].backColor.set(0x00ff00)
                cardProxyConfigs[i].frontImgUrl = 'checkerboard.png'
                cardProxyConfigs[i].backImgUrl = ''
            } else {
                cardProxyConfigs[i].position.set((i - (visibleCardCount-1) / 2) * 40, 0, 0)
                cardProxyConfigs[i].rotation.set(0, 1, 0)
                cardProxyConfigs[i].scale.set(30, 30)
                cardProxyConfigs[i].frontColor.set(0xff0000)
                cardProxyConfigs[i].backColor.set(0x00ff00)
                cardProxyConfigs[i].frontImgUrl = ''
                cardProxyConfigs[i].backImgUrl = ''
            }
        }
    }

    return new ProxyGroup(visibleCardCount, false, onStatic, onFocusIndex)
}

export function testStyle2() {
    const visibleCardCount = 6
    const onStatic = function (containerProxyConfig: ContainerProxyConfig, cardProxyConfigs: Array<CardProxyConfig>) {
        containerProxyConfig.position.set(0, -((visibleCardCount-1) / 2) * 70, 0)
        containerProxyConfig.rotation.set(0, 0, 0)
        for (let i = 0; i < visibleCardCount; i++) {
            cardProxyConfigs[i].position.set(0, i * 70, 0)
            cardProxyConfigs[i].rotation.set(0, 0, 0)
            cardProxyConfigs[i].scale.set(60, 60)
            cardProxyConfigs[i].frontColor.set(0xc0c0f0)
            cardProxyConfigs[i].backColor.set(0xc0c0f0)
            cardProxyConfigs[i].frontImgUrl = ''
            cardProxyConfigs[i].backImgUrl = ''
        }
        
    }
    const onFocusIndex = function (containerProxyConfig: ContainerProxyConfig, cardProxyConfigs: Array<CardProxyConfig>,
        isCurrentFocused: boolean, oldFocusIndex: number, newFocusIndex: number) {
        containerProxyConfig.position.set(0, 1-newFocusIndex * 70, 0)
        containerProxyConfig.rotation.set(0, 0, 0)
        for (let i = 0; i < visibleCardCount; i++) {
            cardProxyConfigs[i].position.set(0, i * 70, 0)
            cardProxyConfigs[i].rotation.set(0, 0, 0)
            cardProxyConfigs[i].scale.set(60, 60)
            cardProxyConfigs[i].frontColor.set(0xc0c0c0)
            cardProxyConfigs[i].backColor.set(0xc0c0c0)
            cardProxyConfigs[i].frontImgUrl = ''
            cardProxyConfigs[i].backImgUrl = ''
            if (i == newFocusIndex) {
                cardProxyConfigs[i].position.set(0, i * 70, 20)
                cardProxyConfigs[i].rotation.set(-0.7, 0, 0)
                cardProxyConfigs[i].scale.set(80, 80)
                cardProxyConfigs[i].frontColor.set(0xff0000)
                cardProxyConfigs[i].backColor.set(0xc0c0c0)
                cardProxyConfigs[i].frontImgUrl = 'checkerboard.png'
                cardProxyConfigs[i].backImgUrl = ''
            }
        }
    }

    return new ProxyGroup(visibleCardCount, false, onStatic, onFocusIndex)
}

var CAROUSEL_ANGLE = 0

export function testStyle3() {
    const radius = 100
    const cardHeight = 60
    const cardWidth = 40
    const visibleCardCount = 10
    const onStatic = function (containerProxyConfig: ContainerProxyConfig, cardProxyConfigs: Array<CardProxyConfig>) {
        CAROUSEL_ANGLE = 0
        containerProxyConfig.position.set(0, 0, 0)
        containerProxyConfig.rotation.set(0, CAROUSEL_ANGLE, 0)
        for (let i = 0; i < visibleCardCount; i++) {
            let angle = i*Math.PI*2/visibleCardCount + Math.PI/2
            cardProxyConfigs[i].position.set(Math.cos(angle) * radius, 0, Math.sin(angle) * radius)
            cardProxyConfigs[i].rotation.set(0, -angle + Math.PI / 2, 0)
            cardProxyConfigs[i].scale.set(cardWidth, cardHeight)
            cardProxyConfigs[i].frontColor.set(new THREE.Color(1, i/visibleCardCount, 0))
            cardProxyConfigs[i].backColor.set(0xa0a0a0)
            cardProxyConfigs[i].frontImgUrl = ''
            cardProxyConfigs[i].backImgUrl = ''
        }
        
    }
    const onFocusIndex = function (containerProxyConfig: ContainerProxyConfig, cardProxyConfigs: Array<CardProxyConfig>,
        isCurrentFocused: boolean, oldFocusIndex: number, newFocusIndex: number) {
        if (oldFocusIndex < newFocusIndex) {
            oldFocusIndex += visibleCardCount
        }
        console.log(oldFocusIndex - newFocusIndex, oldFocusIndex - newFocusIndex > visibleCardCount / 2)
        CAROUSEL_ANGLE += (newFocusIndex - oldFocusIndex)*Math.PI*2/visibleCardCount
        // let containerAngle = newFocusIndex*Math.PI*2/visibleCardCount
        if (oldFocusIndex - newFocusIndex > visibleCardCount / 2) {
            CAROUSEL_ANGLE += Math.PI*2
        }
        containerProxyConfig.rotation.set(0, CAROUSEL_ANGLE, 0)
        for (let i = 0; i < visibleCardCount; i++) {
            let angle = i*Math.PI*2/visibleCardCount + Math.PI/2
            cardProxyConfigs[i].position.set(Math.cos(angle) * radius, 0, Math.sin(angle) * radius)
            cardProxyConfigs[i].frontImgUrl = ''
            // cardProxyConfigs[i].rotation.set(0, Math.PI / 2, 0)
            if (i == newFocusIndex) {
                cardProxyConfigs[i].frontImgUrl = 'checkerboard.png'
                cardProxyConfigs[i].position.set(Math.cos(angle) * (radius + 10), 0, Math.sin(angle) * (radius + 10))
            }
        }
    }

    return new ProxyGroup(visibleCardCount, true, onStatic, onFocusIndex)
}


export function testStyle4() {
    const radius = 100
    const cardHeight = 60
    const cardWidth = 40
    const visibleCardCount = 7
    const angle = 0.3
    const ygap = 15
    const onStatic = function (containerProxyConfig: ContainerProxyConfig, cardProxyConfigs: Array<CardProxyConfig>) {
        containerProxyConfig.position.set(0, 80, 0)
        containerProxyConfig.rotation.set(0, visibleCardCount * angle / 2, 0)
        for (let i = 0; i < visibleCardCount; i++) {
            let a = i*angle + Math.PI / 2
            cardProxyConfigs[i].position.set(Math.cos(a) * radius, -i*ygap, Math.sin(a) * radius)
            cardProxyConfigs[i].rotation.set(Math.PI/2, Math.PI, -a-Math.PI/2)
            cardProxyConfigs[i].scale.set(cardWidth, cardHeight)
            cardProxyConfigs[i].frontColor.set(new THREE.Color(1, i/visibleCardCount, 0))
            cardProxyConfigs[i].backColor.set(0xa0a0a0)
            cardProxyConfigs[i].frontImgUrl = ''
            cardProxyConfigs[i].backImgUrl = ''
        }
        
    }
    const onFocusIndex = function (containerProxyConfig: ContainerProxyConfig, cardProxyConfigs: Array<CardProxyConfig>,
        isCurrentFocused: boolean, oldFocusIndex: number, newFocusIndex: number) {

        containerProxyConfig.rotation.set(0, (newFocusIndex) * angle, 0)
        for (let i = 0; i < visibleCardCount; i++) {
            let a = i*angle + Math.PI / 2
            cardProxyConfigs[i].position.set(Math.cos(a) * radius, -i*ygap, Math.sin(a) * radius)
            cardProxyConfigs[i].rotation.set(Math.PI/2, Math.PI, -a-Math.PI/2)
            cardProxyConfigs[i].scale.set(cardWidth, cardHeight)
            cardProxyConfigs[i].frontColor.set(new THREE.Color(1, i/visibleCardCount, 0))
            cardProxyConfigs[i].backColor.set(0xa0a0a0)
            cardProxyConfigs[i].frontImgUrl = ''
            // cardProxyConfigs[i].backImgUrl = ''
            // cardProxyConfigs[i].rotation.set(0, Math.PI / 2, 0)
            if (i == newFocusIndex) {
                cardProxyConfigs[i].frontImgUrl = 'checkerboard.png'
                cardProxyConfigs[i].position.set(Math.cos(a) * (radius + 20), -i*ygap, Math.sin(a) * (radius + 20))
            }
        }
    }

    return new ProxyGroup(visibleCardCount, false, onStatic, onFocusIndex)
}


export function testStyle5() {
    const radius = 100
    const cardHeight = 160
    const cardWidth = 80
    const visibleCardCount = 3
    const angle = 0.3
    const ygap = 15
    const onStatic = function (containerProxyConfig: ContainerProxyConfig, cardProxyConfigs: Array<CardProxyConfig>) {
        containerProxyConfig.position.set(0, 0, 0)
        containerProxyConfig.rotation.set(-0.8, 0, 0)
        for (let i = 0; i < visibleCardCount; i++) {
            cardProxyConfigs[i].position.set((i-(visibleCardCount-1)/2) * (cardWidth+10), (i-(visibleCardCount-1)/2) * 10, 0)
            cardProxyConfigs[i].rotation.set(0, 0, 0)
            cardProxyConfigs[i].scale.set(cardWidth, cardHeight)
            cardProxyConfigs[i].frontColor.set(new THREE.Color(1, i/visibleCardCount, 0))
            cardProxyConfigs[i].backColor.set(0xa0a0a0)
            cardProxyConfigs[i].frontImgUrl = ''
            cardProxyConfigs[i].backImgUrl = ''
        }
        
    }
    const onFocusIndex = function (containerProxyConfig: ContainerProxyConfig, cardProxyConfigs: Array<CardProxyConfig>,
        isCurrentFocused: boolean, oldFocusIndex: number, newFocusIndex: number) {
        for (let i = 0; i < visibleCardCount; i++) {
            if (i == newFocusIndex) {
                cardProxyConfigs[i].position.set((i-(visibleCardCount-1)/2) * (cardWidth+10), (i-(visibleCardCount-1)/2) * 10, 30)
                cardProxyConfigs[i].scale.set(cardWidth + 20, cardHeight + 10)
                cardProxyConfigs[i].frontImgUrl = 'checkerboard.png'
            } else {
                cardProxyConfigs[i].position.set((i-(visibleCardCount-1)/2) * (cardWidth+10), (i-(visibleCardCount-1)/2) * 10, 0)
                cardProxyConfigs[i].scale.set(cardWidth, cardHeight)
                cardProxyConfigs[i].frontImgUrl = ''
            }
        }
    }

    return new ProxyGroup(visibleCardCount, false, onStatic, onFocusIndex)
}