/**
 * 
 * 管理所有材质
 * 
 */

import * as THREE from 'three'
import Card from './Card'

type TextureInfo = {
    texture: THREE.Texture,
    imgUrl: string,
    lastUsedTime: number
}

export default class TexturePool {

    pool: { [imgUrl: string]: TextureInfo }
    cards: Array<Card>
    maxTextureCount: number

    constructor(cards: Array<Card>, maxTextureCount: number = 100) {
        this.pool = {}
        this.cards = cards
        this.maxTextureCount = maxTextureCount

        // 每隔固定时间，注销一些材质
        setInterval(() => {
            this.destructSomeTextures()
        }, 10000)
    }

    getTexture(imgUrl: string | null = null): THREE.Texture {
        if (!imgUrl) {
            imgUrl = ''
        }
        let pooledTex = this.pool[imgUrl]
        if (pooledTex == undefined) {
            pooledTex = {
                texture: imgUrl == '' ? 
                new THREE.TextureLoader().load('empty_texture.png') : new THREE.TextureLoader().load(imgUrl),
                imgUrl: imgUrl,
                lastUsedTime: Date.now() // 创建该材质的时间是现在
            }
            this.pool[imgUrl] = pooledTex
        } else {
            pooledTex.lastUsedTime = Date.now() // 最近一次访问该材质的时间是现在
        }
        return pooledTex.texture
    }



    /**
     * 
     * 隔一段时间就自动注销一些材质，防止材质过多
     * 注销规则：1. 所有正在使用的材质不能销毁；2. 按LRU规则
     * 
     */
    destructSomeTextures() {
        let numOfTexturesInPool = Object.keys(this.pool).length
        if (numOfTexturesInPool > this.maxTextureCount) {
            let destructableTextures: Array<TextureInfo> = []
            for (let url in this.pool) {
                // 判断是否有 card 在使用该材质
                let destructableFlg = true
                for (let cardIndex = 0; cardIndex < this.cards.length; cardIndex++) {
                    if (this.cards[cardIndex].matFront.map == this.pool[url].texture || this.cards[cardIndex].matBack.map == this.pool[url].texture) {
                        destructableFlg = false
                        break
                    }
                }
                if (destructableFlg) {
                    // 未被使用
                    destructableTextures.push(this.pool[url])
                }
            }
            if (destructableTextures.length > 0) {
                // 按最近使用时间排序
                destructableTextures.sort((a, b) =>  a.lastUsedTime - b.lastUsedTime)
                for (let i = 0; i < destructableTextures.length && i < numOfTexturesInPool - this.maxTextureCount; i++) {
                    // 注销材质
                    destructableTextures[i].texture.dispose()
                    // 在 pool 中删除材质索引
                    delete this.pool[destructableTextures[i].imgUrl]
                }
            }
        }
    }
}